import { MerchantsService } from "@/common/merchants.service";

import { FETCH_MERCHANTS } from "@/store/actions.type";

import { SET_CURRENT_LOCATION, FETCH_MERCHANTS_END, SET_AUTH } from "@/store/mutations.type";

const initialState = { 
  merchants: [], 
  isLoadingMerchant: true,
  coordinate: {
    lat: null,
    lon: null
  }
};
export const state = { ...initialState };


export const actions = {
  async [FETCH_MERCHANTS](context) {
    const { data } = await MerchantsService.query('', {
      lat: state.coordinate.lat,
      lng: state.coordinate.lon,
    });
    // console.log(data.data);
    console.log(Date(), 'Fetched home', data);
    // console.log(Date(), 'Number of unread', context.rootState.auth.user.unreadInbox);
    // context.rootState.auth.user.unreadInbox = data.user.unreadInbox;
    // console.log(context)
    // console.log(Date(), 'Number of unread', context.rootState.auth.user.unreadInbox);
    
    context.commit(FETCH_MERCHANTS_END, data.merchants);
    context.commit(SET_AUTH, {
      user: data.user,
      fullLoad: false,
    });
    return data;
  },
};


export const mutations = {
  [FETCH_MERCHANTS_END](state, merchants) {
    state.merchants = merchants;
    state.isLoadingMerchant = false;
  },

  [SET_CURRENT_LOCATION](state, coordinate) {
    state.coordinate = {
      lat: coordinate.lat,
      lon: coordinate.lon,
    }
  },
};


const getters = {
  merchants(state) {
    return state.merchants;
  },
  isLoadingMerchant(state) {
    return state.isLoadingMerchant;
  },
};


export default {
  state,
  actions,
  mutations,
  getters
};



