<template>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <router-link class="navbar-brand" :to="{ name: 'home' }">
        STASH
      </router-link>
      <ul v-if="isAuthenticated" class="nav navbar-nav pull-xs-right">
        <li class="nav-item" v-ripple>
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'wallet' }"
          >
            <i class="ion-cash"></i>&nbsp; <b>{{ (currentUser.wallet.value) | currency }}</b>
          </router-link>
        <li class="nav-item">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'inbox' }"
          >
            <b><span v-if="currentUser.unreadInbox > 0">{{ currentUser.unreadInbox }}&nbsp;&nbsp;</span><i class="ion-android-mail iconBigger"></i></b>
          </router-link>
        </li>
        <li class="nav-item" v-ripple v-if="currentCart.items.length >= 1">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'cart' }"
          >
            <b class="currentCart"><i class="ion-android-cart"></i></b>
          </router-link>
        </li>
      </ul>
      <ul v-if="!isAuthenticated" class="nav navbar-nav pull-xs-right">
        <li class="nav-item" v-ripple>
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'login' }"
          >
            <i class="ion-cash"></i>&nbsp; <b>SGD 0.00</b>
          </router-link>
        <!-- <li class="nav-item">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'inbox' }"
          >
            <b><i class="ion-gear-a iconBigger"></i></b>
          </router-link>
        </li> -->
        <li class="nav-item" v-ripple v-if="currentCart.items.length >= 1">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'cart' }"
          >
            <b class="currentCart"><i class="ion-android-cart"></i></b>
          </router-link>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "GeneralHeader",

  data() {
    return {
      lastMessage: -1,
      timer: false,
    }
  },

  computed: {
    ...mapGetters(["isAuthenticated", "currentCart", "currentUser"])
  },

  created: function() {
    this.refreshContent();
    this.timer = setInterval(this.refreshContent, 1 * 30 * 1000);// Minutes * Seconds * 1000
  },

  destroyed() {
    console.log(Date(), 'Removing auto refresh');
  },

  methods: {
    refreshContent() {
      console.log(Date(), 'Current user unread inbox from header', this.currentUser.lastMessage, this.lastMessage)

      if(this.lastMessage < 0 || !this.lastMessage == undefined ){
        console.log(Date(), 'Setting last message because 0 init', this.currentUser.lastMessage)
        if(this.currentUser.lastMessage != undefined){
          this.lastMessage = this.currentUser.lastMessage
        }else{
          this.lastMessage = -1
        }
        return
      }

      if(this.currentUser.lastMessage > this.lastMessage){
        
        this.$swal({
          type: 'info',
          title: 'New message',
          showCancelButton: false,
          confirmButtonText: 'View now',
          showCloseButton: true,
          showLoaderOnConfirm: true
        }).then((result) => {
          if(result.value) {
            
            this.$ga.event('home', 'inbox', 'new_message', true);
            
            this.$router.push({ name: 'inbox', })
          } else {
            
            this.$ga.event('home', 'inbox', 'new_message', false);
            
            
            return 0;
          }
        })

        this.lastMessage = this.currentUser.lastMessage


      }

    },

    cancelAutoUpdate() { 
      clearInterval(this.timer) 
    },
  }
};
</script>

<style scoped>
  .navbar{
    background-color: #ffba00;
    color: white;
    border-color: #e7e7e7;
  }

  .navbar-brand {
    color: white !important;
  }

  li.nav-item{
    background-color: white !important;
    color: #ffba00 !important;
    padding: 0 15px;
    vertical-align: center;
    height: 100%;
    border-radius: 50px;
  }
  
  
</style>


